:root{
    --main-color:#3f51b5;
}

.parent div .onesthalf h1{
    margin:0px;

}
.hireAcamp{
    color:var(--main-color);
}
.duration{
    margin-left: 20px;
    color:var(--main-color);
}
.duration2{
    margin-left: 60px;
    color:var(--main-color);
}
.text {
    color: black;
}
h4{
    margin-top: 10px;
}
.project .p-format{
    margin-left: 50px;
}
.intern-details .p-format{
    margin-left: 50px;
}

/* NOW FOR THE 2nd half */
.main{
    display: flex;
}

.space {
    margin: 0;
    background-color: transparent;
    border: none;
    height: 19px;
}
.other-half .p-format2{
    margin-left: 70px;
    font-size: large;
    font-weight: 600;
}
.record-icon1{
    margin-left: 100px;
}
.record-icon2{
    margin-left: 123px;
}
.record-icon3{
    margin-left: 71px;
}
.other-half .btn-inside{
    background-color: transparent;
    height: 50px;
    width: 100px;
    margin: 10px;
    flex-wrap: wrap;
    color: var(--main-color);
    border: 2px solid var(--main-color);
    border-radius: 12px;
}
.other-half .btn{
    display: flex;
    flex-wrap: wrap;
}
.btn-inside:focus{
    outline:none;
}
.btn-inside2:focus{
    outline:none;
}
.other-half .btn-inside2{
    background-color: transparent;
    height: 50px;
    width: 150px;
    margin: 10px;
    flex-wrap: wrap;
    color: var(--main-color);
    border: 2px solid var(--main-color);
    border-radius: 12px;
}
.btn-inside:hover, .btn-inside2:hover{
    transform: scale(1.1);
}
 .a-links a{
    color:var(--main-color);
}


 