@import url(https://fonts.googleapis.com/css?family=Raleway&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.box {
    width: 90%;
    height: auto;
    position: relative;
    top: -20vh;
    margin-left: auto;
    margin-right: auto;
    background-color: white;
    border-radius: 5px;
    box-shadow: 5px 5px 30px #271B1B
}

.profile {
    width: 250px;
    height: 270px;
    padding: 10px;
    position: relative;
    top: -25vh;
    margin-left: auto;
    margin-right: auto;
    background-color: white;
    border-radius: 5px;
    box-shadow: 5px 5px 30px #271B1B
}
.image {
    width: 92%;
    margin-left: auto;
    margin-right: auto;
    border-radius: 3%;
}
.text {
    margin-top: 10px;
    font-weight: 500;
    text-align: center;
    /* position: relative; */
    top: 90px;
}
.social {
    text-align: center;
    margin-top: 30px;    
}

.about {
    position: relative;
    top: -15vh;
}
.col, .col-sm-6, .col-md-4 > .img {
  padding: 5px;
}
.intro-part{
    margin-bottom: 20px;
}
.intro-part p{
    font-size: 25px;
    font-weight: 500;
    color:var(--main-color);
}
.intro-part .my-name{
    color:black;
    font-size: 36px;
}
.internal-details1, .internal-details2{
    margin-left: 20px;
    font-size: large;
}
.location{
    margin-left: 10%;
}
.linkedIn{
    margin-left: 19%;
}
.github{
    margin-left: 8%;
}
.intro-part a{
    color:black;
}
a:hover{
    text-decoration: none;
    color: var(--main-color);
}


:root{
    --main-color:#3f51b5;
}

.parent div .onesthalf h1{
    margin:0px;

}
.hireAcamp{
    color:#3f51b5;
    color:var(--main-color);
}
.duration{
    margin-left: 20px;
    color:#3f51b5;
    color:var(--main-color);
}
.duration2{
    margin-left: 60px;
    color:#3f51b5;
    color:var(--main-color);
}
.text {
    color: black;
}
h4{
    margin-top: 10px;
}
.project .p-format{
    margin-left: 50px;
}
.intern-details .p-format{
    margin-left: 50px;
}

/* NOW FOR THE 2nd half */
.main{
    display: flex;
}

.space {
    margin: 0;
    background-color: transparent;
    border: none;
    height: 19px;
}
.other-half .p-format2{
    margin-left: 70px;
    font-size: large;
    font-weight: 600;
}
.record-icon1{
    margin-left: 100px;
}
.record-icon2{
    margin-left: 123px;
}
.record-icon3{
    margin-left: 71px;
}
.other-half .btn-inside{
    background-color: transparent;
    height: 50px;
    width: 100px;
    margin: 10px;
    flex-wrap: wrap;
    color: #3f51b5;
    color: var(--main-color);
    border: 2px solid #3f51b5;
    border: 2px solid var(--main-color);
    border-radius: 12px;
}
.other-half .btn{
    display: flex;
    flex-wrap: wrap;
}
.btn-inside:focus{
    outline:none;
}
.btn-inside2:focus{
    outline:none;
}
.other-half .btn-inside2{
    background-color: transparent;
    height: 50px;
    width: 150px;
    margin: 10px;
    flex-wrap: wrap;
    color: #3f51b5;
    color: var(--main-color);
    border: 2px solid #3f51b5;
    border: 2px solid var(--main-color);
    border-radius: 12px;
}
.btn-inside:hover, .btn-inside2:hover{
    transform: scale(1.1);
}
 .a-links a{
    color:#3f51b5;
    color:var(--main-color);
}


 
.horizontal {
    background-color:  var(--main-color);
    height:5px;
    margin: 0;
    border: none;
    width: 100%;
}
.resume {
    text-align: left;
}

.icon {
    color: var(--main-color);
    font-size: 1.2rem !important; 
}
.cor {
    height: 70vh;
    z-index: -1;
}
.w-100 {
    height: 60vh!important;
}
html {
    scroll-behavior: smooth;
}
.black {
    background-color: black;
    width: 100%;
    height: 60px;
    position: fixed;
    top: 0px;
    z-index: -1;
}
.white {
    margin-top: 10px;
    width: 95%;
    height: 50px;
    position: relative;
    text-align: center;
    margin-left: auto;
    margin-right: auto;
}
.nav-border {
    border-radius: 5px;
    box-shadow: 5px 5px 30px #271B1B;
    z-index : 1
}
.heading {
    font-family: 'Raleway', sans-serif;
    font-size: x-large;
}

.scroll {
    -webkit-animation: steps(3);
            animation: steps(3);
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

