@import url('https://fonts.googleapis.com/css?family=Raleway&display=swap');
html {
    scroll-behavior: smooth;
}
.black {
    background-color: black;
    width: 100%;
    height: 60px;
    position: fixed;
    top: 0px;
    z-index: -1;
}
.white {
    margin-top: 10px;
    width: 95%;
    height: 50px;
    position: relative;
    text-align: center;
    margin-left: auto;
    margin-right: auto;
}
.nav-border {
    border-radius: 5px;
    box-shadow: 5px 5px 30px #271B1B;
    z-index : 1
}
.heading {
    font-family: 'Raleway', sans-serif;
    font-size: x-large;
}

.scroll {
    animation: steps(3);
}