.box {
    width: 90%;
    height: auto;
    position: relative;
    top: -20vh;
    margin-left: auto;
    margin-right: auto;
    background-color: white;
    border-radius: 5px;
    box-shadow: 5px 5px 30px #271B1B
}

.profile {
    width: 250px;
    height: 270px;
    padding: 10px;
    position: relative;
    top: -25vh;
    margin-left: auto;
    margin-right: auto;
    background-color: white;
    border-radius: 5px;
    box-shadow: 5px 5px 30px #271B1B
}
.image {
    width: 92%;
    margin-left: auto;
    margin-right: auto;
    border-radius: 3%;
}
.text {
    margin-top: 10px;
    font-weight: 500;
    text-align: center;
    /* position: relative; */
    top: 90px;
}
.social {
